export default {
	data() {
		return {
			type:'', //求购单分类
			pageNum:0, //当前页数
			mydata:[], //求购单数组
			seartext:'', //搜索关键字
		};
	},
	onShow:function(){
		this.myajax()
	},
	methods: {
		// 点击搜索
		gosearch:function(){
			this.pageNum++
			this.mydata=[]
			this.myajax()
		},
		// 初始化数据
		myajax:function(){
			var that=this
			this.pageNum++
			uni.showLoading({
			    title: '加载中',
				mask:true
			});
			uni.request({
			    url: 'https://www.example.com/request', //仅为示例，并非真实接口地址。
			    data: {
			        text: 'uni.request'
			    },
			    success: (res) => {
			        console.log(res.data);
					that.mydata.push(res.data)
			    }
			});
			setTimeout(function() {
				uni.hideLoading()
			}, 1000);
		},
		opennew: function(id) {
			uni.navigateTo({
				url: '../' + id + '/' + id
			});
		},
		opennew02: function(id) {
			uni.switchTab({
				url: '../' + id + '/' + id
			});
		},
		//流标
		liubiao: function(id) {
			uni.showModal({
				title: '温馨提示',
				content: '确定流标此求购单吗',
				confirmColor:'#008A05',
				success: res => {
					if (res.confirm) {
						console.log('用户点击确定');
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 展示求购单类型
		showtype:function(){
			var that=this
			uni.showActionSheet({
			    itemList: ['全部', '已过期', '审核中'],
			    success: function (res) {
			        console.log('选中了第' + (res.tapIndex + 1) + '个按钮');
					that.gosearch()
			    },
			    fail: function (res) {
			        console.log(res.errMsg);
			    }
			});
		}
	}
};
